@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);

* {
  font-family: "Noto Sans KR";
}

.main {
  box-sizing: border-box;
  margin: 0;
  width: 360px;
  margin: 0 auto;
  text-align: center;
  height: 100vh;
  background: no-repeat url("image/schoolBackgound.png") center/cover;
}

.layer {
  height: 100vh;
  width: 360px;
  background: rgba(255, 255, 255, 0.9);
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.comment {
  margin: 0px 0px 20px 0px;
  font-size: 20px;
  color: #207d60;
}

.warning {
  width: 100%;
  height: 216px;
  background-color: #2b2823;
  padding-top: 15px;
  margin: 20px 0 30px 0;
}

.warning_text {
  color: white;
  font-size: 20px;
  margin: 0;
}

.warning_meme {
  width: 189px;
  height: 96px;
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  margin-bottom: 15px;
}

.text {
  font-size: 20px;
  color: #207d60;
}

.caution {
  color: #207d60;
  font-size: 12px;
}

form {
  margin-top: 7px;
  position: relative;
}

input {
  width: 326px;
  height: 52px;
  border: 1px solid #207d60;
  border-radius: 35px;
  background-color: transparent;
  text-align: center;
}

input::placeholder {
  font-weight: bold;
  font-size: 12px;
  color: #207d60;
  padding-right: 40px;
}

input:focus {
  outline: none;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

.submitBtn {
  width: 64.13px;
  height: 34.67px;
  position: absolute;
  right: 27px;
  top: 50%;
  transform: translate(0, -50%);
  background-color: #207d60;
  color: white;
  border: none;
  border-radius: 35px;
  font-size: 12px;
  cursor: pointer;
}

.besider {
  margin: 0px 0px 20px 0px;
  font-size: 8px;
  font-weight: lighter;
  color: #207d60;
}

.footer {
  position: absolute;
  bottom: 0;
  margin-top: 31px;
  width: 100%;
}

.footer_box {
  position: relative;
  bottom: 0;
  height: 31px;
  width: 100%;
  background-color: #207d60;
}
